.squadCard {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  position: relative;
}

.buttonWrapper {
  position: absolute;
  top: 0;
  right: 0;
}

.infoBlock {
  -ms-flex: 1 1;
      flex: 1 1;
}

.title {
  text-align: center;
}

.namesList,
.statsList {
  list-style-type: none;
  padding: 0;
}

.namesList>li:before {
  content: ' * ';
}

.statsList {
  text-align: center;
}


